<template>
  <div class="archives">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a download  class="f-small main-c mr5" href="../gy-files/template/档案批量导入模板.xlsx" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
        <div @click="showStorageDialog()" class="btn-main-deepen mr15">档案入库</div>
        <div @click="$router.push('/personnel/archives/type')" class="btn-main-deepen mr15">类别管理</div>
        <div @click="showPlaceDialog" class="btn-main-deepen">位置管理</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="13%">档案编号</th>
          <th width="17%">类别</th>
          <th width="20%">归档内容</th>
          <th width="12%">保存期限</th>
          <th width="8%">状态</th>
          <th width="12%">位置</th>
          <th width="18%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.dossierNo || '--'}}</td>
          <td>{{item.dossierType}}</td>
          <td>{{item.content}}</td>
          <td>{{$enums.StorageLife.getName(item.storageLife)}}</td>
          <td>{{$enums.DossierStatus.getName(item.dossierStatus)}}</td>
          <td>{{item.dossierLocation}}</td>
          <td>
            <span @click="showLendDialog('LOAN',item)" v-show="item.dossierStatus === 'STOCK'" class="oper">借出</span>
            <span @click="showLendDialog('STOCK',item)" v-show="item.dossierStatus === 'LOAN'" class="oper">归还</span>
            <span @click="showChangeDialog(item)" class="oper">变动记录</span>
            <span @click="showStorageDialog(item)" class="oper">详细内容</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--位置管理-->
      <el-dialog width="540px" title="位置管理" class="place-dialog" :visible.sync="placeDialog">
        <span>关键字</span>
        <el-input @keyup.enter.native="$search(place)" v-model="place.query.keywords" placeholder="位置名称" class="input ml10">
          <el-button @click="$search(place)" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <div @click="operPlace('add')" class="btn-submit">添加位置</div>
        <resize-table>
        <table class="table palce-table mt15">
          <thead>
          <tr>
            <th width="65%">位置</th>
            <th width="35%">操作</th>
          </tr></thead>
          <tbody>
          <tr v-if="showAddPlace">
            <td>
              <el-input v-model="ItemPlace" size="small" placeholder="请输入位置"></el-input>
            </td>
            <td>
              <span @click="addPlaceFun" class="oper">完成</span>
              <span @click="showAddPlace = false" class="oper">取消</span>
            </td>
          </tr>
          <tr v-for="(item,key) in place.list">
            <td>
              <p v-show="item.itemStatus !== true">{{item.name}}</p>
              <el-input v-show="item.itemStatus === true" v-model="ItemPlace" size="small" placeholder="请输入位置"></el-input>
            </td>
            <td>
              <span v-show="item.itemStatus !== true" @click="operPlace('edit',item,key)" class="oper">编辑</span>
              <span v-show="item.itemStatus === true && showAddPlace" @click="addPlaceFun" class="oper">完成</span>
              <span v-show="item.itemStatus === true && !showAddPlace" @click="editPlaceFun" class="oper">完成</span>
              <span v-show="item.itemStatus === true" @click="cancelPlaceFun(item,key)" class="oper">取消</span>
            </td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!place.noData"></no-data>
        <paging :data.sync="place"></paging>
      </el-dialog>
      <!--档案入库 详情-->
      <el-dialog width="540px" :title="!storage.id ? '档案入库': '档案详情'" class="storage-dialog" :visible.sync="storageDialog">
        <div class="storage-content">
          <div class="item">
            <span class="title imp">保存期限</span>
            <el-radio-group v-model="storage.storageLife">
              <el-radio :key="key" v-for="(item,key) in $enums.StorageLife.list" :label="item.value" border>{{item.label}}&nbsp;&nbsp;&nbsp;&nbsp;</el-radio>
            </el-radio-group>
          </div>
          <div class="item">
            <span class="title imp">归档内容</span>
            <el-input  v-model="storage.content" placeholder="请输入归档内容" class="input"></el-input>
          </div>
          <div class="item">
            <span class="title imp">档案类别</span>
            <el-select  v-model="storage.dossierTypeId" class="input" placeholder="请选择档案类别">
              <el-option
                  v-for="item in docList"
                  :label="item.name" :value="item.id" :key="item.id"
              ></el-option>
            </el-select>
          </div>
          <div class="item">
            <span class="title imp">位置</span>
            <el-select v-model="storage.dossierLocationId" default-first-option class="input" filterable placeholder="请选择档案位置">
              <el-option
                  v-for="item in docLocationList"
                  :label="item.name" :value="item.id" :key="item.id"
              ></el-option>
            </el-select>
          </div>
          <div class="item">
            <span class="title">详细内容</span>
            <el-input  type="textarea" class="input" v-model="storage.remark" :rows="5" placeholder="请输入内容"></el-input>
          </div>
          <div v-show="!storage.id" class="btns">
            <div @click="addStorage" class="btn-submit btn-add mr15">确定</div>
            <div @click="storageDialog = false" class="btn-clear">取消</div>
          </div>
          <div v-show="storage.id" class="btns">
            <div @click="saveStorage" class="btn-submit btn-save mr15">保存</div>
            <div @click="storageDialog = false" class="btn-clear">取消</div>
          </div>
        </div>
      </el-dialog>
      <!--档案借出-->
      <el-dialog width="540px" :title="'档案' + title " class="storage-dialog" :visible.sync="lendDialog">
        <div v-show="title === '归还'" class="item">
          <span class="title imp">借出人</span>
          <el-input type="text" disabled class="input" :value="lendReturnForm.userName"/>
        </div>
        <div v-show="title === '借出'" class="item">
          <span class="title imp">{{title}}人</span>
          <el-select
              v-model="lendReturnForm.userId"
              class="input"
              filterable
              default-first-option
              remote
              clearable
              reserve-keyword
              placeholder="请输入教职工关键词"
              :remote-method="getStaff"
              :loading="loading">
            <el-option
                v-for="item in staffList"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{item.idCard}}/{{item.phone}}</span>
            </el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title imp">{{title}}时间</span>
          <el-date-picker v-model="lendReturnForm.date" type="date" class="input" placeholder="选择日期">
          </el-date-picker>
        </div>
        <div class="btns">
          <div v-show="title === '借出'" @click="lendFun" class="btn-submit mr15">确认</div>
          <div v-show="title === '归还'" @click="returnFun" class="btn-submit mr15">确认</div>
          <div @click="lendDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <!--档案变动-->
      <el-dialog width="540px" title="档案变动记录" class="storage-dialog" :visible.sync="changeDialog">
        <resize-table>
        <table class="table borrowed-table">
          <thead>
          <tr>
            <th>变动类型</th>
            <th>借出人</th>
            <th>借出/归还时间</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in borrowed.list">
            <td>{{$enums.DossierStatus.getName(item.dossierStatus)}}</td>
            <td>{{item.userName}}</td>
            <td>{{item.recordDate | substrTime}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!borrowed.noData"></no-data>
        <paging :data.sync="borrowed"></paging>
      </el-dialog>
    </main-part>

  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "档案管理", path: ""}],
        searchData: [
          {type: 'select',title: '类别', model: 'dossierTypeId', option: []},
          {type: 'select',title: '保存期限', model: 'storageLife', option: this.$enums.StorageLife.list},
          {type: 'select',title: '状态', model: 'dossierStatus', option: this.$enums.DossierStatus.list},
          {type: 'input', placeholder: '档案编号/归档内容'}],
        table: {
          url: '/dossier/queryDossierPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            dossierTypeId: null,
            storageLife: null,
            dossierStatus: null,
            keywords: null
          }
        },
        // 位置管理弹窗
        placeDialog: false,
        // 添加位置表单显示隐藏
        showAddPlace: false,
        // 位置列表查询
        place: {
          url: '/dossier/queryDossierLocationPage.do',
          el: '.palce-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: null
          }
        },
        // 借还列表查询
        borrowed: {
          url: '/dossier/queryDossierRecordPage.do',
          el: '.borrowed-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            id: null,
            keywords: null
          }
        },
        title: '',
        // 位置表单
        ItemPlace: '',
        ItemPlaceId: '',
        // 档案入库弹窗
        storageDialog: false,
        // 档案类别
        docList: [],
        // 档案位置
        docLocationList: [],
        // 入库表单
        storage:{
          dossierTypeId: null,
          dossierLocationId: null,
          content: '',
          storageLife: 'APPROPRIATE',
          remark: ''
        },
        // 归还借出弹窗
        lendDialog: false,
        lendReturnForm: {
          id: '',
          userId: '',
          userName: '',
          date: null,
        },
        // 档案变动
        changeDialog: false,
        // 所有的教职工
        staffList: [],
        loading: false,
      }
    },
    methods: {
      // 展开档案变动弹窗
      async showChangeDialog(item){
        this.changeDialog = true
        this.borrowed.query.id = item.id
        this.$search(this.borrowed)
      },
      // 展开位置管理弹窗
      showPlaceDialog() {
        this.placeDialog = true
        this.$search(this.place)
      },
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importDossier.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 显示隐藏 编辑档案位置
      operPlace(str,item,key){
        if(str === 'add'){
          this.ItemPlace = ''
          this.ItemPlaceId = ''
          this.showAddPlace = true
        }else{
          this.ItemPlace = item.name
          this.ItemPlaceId = item.id
          this.place.list.forEach((e,i)=>{
            if(e.itemStatus){
              e.itemStatus = false
              this.place.list.splice(i,1,e)
            }
          })
          item.itemStatus = true
          this.place.list.splice(key,1,item)
        }
      },
      // 获取教职工成员
      async getStaff(query){
        if(query !== ''){
          this.loading = true
          let res = await this.$ajax({
            url: '/user/queryStaffPage.do',
            query:{
              keywords: query,
              pageNo: 1,
              pageSize: 10000000
            }
          })
          this.loading = false
          this.staffList = res.list
        }else{
          this.staffList = []
        }
      },
      // 取消编辑
      cancelPlaceFun(item,key){
        item.itemStatus = false
        this.place.list.splice(key,1,item)
      },
      // 添加档案位置
      async addPlaceFun() {
        await this.$ajax({
          url: '/dossier/addDossierLocation.do',
          query: {
            name: this.ItemPlace
          }
        })
        this.$remind('添加成功')
        this.showAddPlace = false
        this.$search(this.place)
      },
      // 修改位置
      async editPlaceFun(){
        await this.$ajax({
          url: '/dossier/updateDossierLocation.do',
          query: {
            name: this.ItemPlace,
            id: this.ItemPlaceId
          }
        })
        this.$remind('编辑成功')
        this.$search(this.place)
      },
      // 档案入库弹窗
      async showStorageDialog(item){
        this.storageDialog = true
        if(item){
          let res = await this.$ajax({
            url: '/dossier/getDossier.do',
            el: '.storage-content',
            query:{
              id: item.id
            }
          })
          this.storage = res
        }else{
          this.storage = {
            dossierTypeId: null,
            dossierLocationId: null,
            content: '',
            storageLife: 'APPROPRIATE',
            remark: ''
          }
        }

      },
      // 获取档案类别
      async getDocType(){
        this.docList = await this.$ajax({
          url: '/dossier/queryDossierTypeList.do',
        })
        this.searchData[0].option = this.docList.map(e=>{
          return {label: e.name,value: e.id}
        })
      },
      // 获取档案位置
      async getDocLocation(){
        this.docLocationList = await this.$ajax({
          url: '/dossier/queryDossierLocationList.do'
        })
      },
      // 添加档案
      async addStorage(){
        if(!this.storage.content){
          this.$message.warning('请输入归档内容')
          return
        }
        if(!this.storage.dossierTypeId){
          this.$message.warning('请选择档案类别')
          return
        }
        if(!this.storage.dossierLocationId){
          this.$message.warning('请选择档案位置')
          return
        }
        await this.$ajax({
          url: '/dossier/addDossier.do',
          el: '.btn-add',
          noText: true,
          query: this.storage
        })
        this.storageDialog = false
        this.$remind('档案入库成功')
        this.$search(this.table)
      },
      // 编辑档案
      async saveStorage(){
        if(!this.storage.content){
          this.$message.warning('请输入归档内容')
          return
        }
        if(!this.storage.dossierTypeId){
          this.$message.warning('请选择档案类别')
          return
        }
        if(!this.storage.dossierLocationId){
          this.$message.warning('请选择档案位置')
          return
        }
        await this.$ajax({
          url: '/dossier/updateDossier.do',
          el: '.btn-save',
          noTrue: true,
          query: this.storage
        })
        this.storageDialog = false
        this.$remind('档案修改成功')
        this.$search(this.table)
      },
      // 展示借出弹窗
      showLendDialog(str,item){
        if(str === 'STOCK'){
          this.title = '归还'
        }else{
          this.getStaff()
          this.title = '借出'
        }
        this.lendDialog = true
        this.lendReturnForm = {
          id: item.id,
          userId: null,
          userName: item.lastLoanName,
          date: null,
        }
      },
      // 档案借出
      async lendFun(){
        if(!this.lendReturnForm.userId){
          this.$message.warning('请选择借出人')
          return
        }
        if(!this.lendReturnForm.date){
          this.$message.warning('请选择借出时间')
          return
        }
        await this.$ajax({
          url: '/dossier/loanDossier.do',
          query: {
            id: this.lendReturnForm.id,
            userId: this.lendReturnForm.userId,
            recordDate: new Date(this.lendReturnForm.date).$Format('yyyy-MM-dd') + ' 00:00:00'
          }
        })
        this.lendDialog = false
        this.$remind('借出成功')
        this.$search(this.table)
      },
      // 档案归还
      async returnFun(){
        if(!this.lendReturnForm.date){
          this.$message.warning('请选择归还时间')
          return
        }
        await this.$ajax({
          url: '/dossier/returnDossier.do',
          query: {
            id: this.lendReturnForm.id,
            userId: this.lendReturnForm.userId,
            recordDate: new Date(this.lendReturnForm.date).$Format('yyyy-MM-dd') + ' 00:00:00'
          }
        })
        this.lendDialog = false
        this.$remind('归还成功')
        this.$search(this.table)
      }
    },
    mounted() {
      this.$search(this.table)
      this.getDocType()
      this.getDocLocation()
    }
  }
</script>
<style lang="sass" scoped>
  .archives
    .place-dialog
      .input
        width: 240px
      .btn-submit
        float: right
    .btn-block
      .main-c
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    .storage-dialog
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .input
        width: calc(100% - 80px)
      .item
        margin-bottom: 20px
      .btns
        text-align: center
</style>
